<template>
  <div class="center resultQuery">
    <div class="center_breadcrumb">
      <Crumb />
    </div>
    <div class="center_content">
      <div class="title">请选择您要查询的课程</div>
      <div class="table-container">
        <ul class="table-ul" :style="{height:tableHeight}">
          <li
            class="table-ul_li"
            v-for="(vo,index) in tableData"
            :key="index"
            :style="{width:oftenWidth}"
          >
            <p>{{vo.year}}</p>
            <ul>
              <li
                v-for="(item,num) in vo.list"
                :key="`${index}-${num}`"
                @click="courseInfo(item)"
                :title="item.courseName"
              >{{item.courseName}}</li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import studentApi from "@/api/studentApi";
export default {
  name: "ResultQuery",
  components: {},
  data() {
    return {
      pageTitle: "成绩查询",
      tableData: [],
      tableHeight: "47px"
    };
  },
  created() {},
  mounted() {
    this.initData();
  },
  computed: {
    oftenWidth() {
      return `${(1 / this.tableData.length) * 100}%`;
    }
  },
  methods: {
    initData() {
      const url = studentApi.getAllCourse;
      this.$get(url).then(res => {
        this.dataProcessing(res);
      });
    },
    dataProcessing(res) {
      const courses = res.courses;
      const yearTerms = res.yearTerms;
      const lengthArr = [];

      yearTerms.forEach(item => {
        item.list = [];
        courses.forEach(vo => {
          if (vo.order === item.order) {
            item.list.push(vo);
          }
        });
        lengthArr.push(item.list.length);
      });

      const maxLength = Math.max.apply(null, lengthArr);
      yearTerms.forEach(item => {
        const lengths = item.list.length;
        if (lengths < maxLength) {
          const leng = maxLength - lengths;
          for (let i = 0; i < leng; i++) {
            item.list.push({
              courseName: "",
              courseId: ""
            });
          }
        }
      });

      this.tableData = yearTerms;
      let tmpArr = [];
      this.tableData.forEach(item => {
        tmpArr.push(item.list.length);
      });
      this.tableHeight = `${Math.max(...tmpArr) * 47 + 100}px`;
    },
    courseInfo(item) {
      if (item.courseId) {
        this.$router.push({
          path: "/resultQuery/resultDetails",
          query: {
            id: item.courseId,
            courseName: item.courseName,
            pageTitle: [this.pageTitle, "成绩详情"]
          }
        });
      }
    },
    headInfo(item) {
      this.pageTitle = item[1];
    }
  }
};
</script>

<style lang='scss'>
@import "../../styles/center.scss";
.resultQuery {
  .title {
    height: 85px;
    line-height: 85px;
    text-align: center;
    color: #fff;
    font-size:24px;
    letter-spacing: 0.2em;
    background-image: url('../../assets/img/bg-OBE.png');
    background-size: 100% 100%;
  }
  .center_content {
    .table-container {
      padding: 30px;
      border-radius: 0 0 10px 10px !important;
    }
  }
  
}
.table-ul {
  width: 100%;
  clear: both;
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  li {
    list-style-type: none;
    height: 46px !important;
    text-align: center;
    line-height: 46px;
    border-right: 0 !important;
    border-bottom: 0 !important;
    box-sizing: border-box;
  }
  .table-ul_li {
    float: left;
    position: relative;
    border: 0 !important;
    background: #e5f7f3;
    padding: 0 10px;
    p {
      width: 100%;
      height: 100%;
      text-align: center;
      vertical-align: center;
      font-size: 12px;
      color: #323232;
      font-weight: 700;
       overflow:hidden;
      text-overflow:ellipsis;
      white-space:nowrap;

    }
    ul {
      width: 100%;
      position: absolute;
      left: 0;
      top: 46px;
      li {
        width: 100%;
        height: 46px;
        font-size: 14px;
        color: #606266;
        cursor: pointer;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        border: 1px solid transparent !important;
        padding: 0 10px;
        &:hover {
          border-color: #03B187 !important;
          color: #03B187 !important;
          box-shadow:0px 2px 10px 0px rgba(3,177,135,0.2);
          border-radius: 5px;
        }
      }
      li:nth-of-type(even) {
        background: #F8F8F8 !important;
      }
    }
  }
  .table-ul_li {
    // border-left: 1px solid #ebeef5;
    ul {
      li {
        // border-left: 1px solid #ebeef5;
      }
    }
  }
}
</style>



